/* Creating the Layout */

.mainCard {
    width: 95%;
    margin: auto;
    margin-top: 75px;
}

#hobbies {
    display: block;
    width: 33.3333%;
    height: 240px;
    margin-left: 0px;
}

#personality {
    display: block;
    width: 33.3333%;
    height: 240px;
    margin-left: 33.33333%;
}

#education {
    display: block;
    width: 33.3333%;
    height: 240px;
    margin-left: 66.66667%
}

/* Mobile Responsiveness */

@media only screen and (max-width: 600px) {
    #hobbies {
        display: block;
        width: 90%;
        height: 240px;
        margin-bottom: 10px;
        margin-left: auto;
        margin-right: auto;
    }
    
    #personality {
