/* Card Styling */

.littleCards {
    border: 10px solid #DFB722;
    background: #8f7227;
    background-image: linear-gradient(300deg, #000000 50%, #8f7227 90%);
    font-family: 'Licorice', cursive;
    color: #DFB722;
    line-height: 230px;
    font-size: 100px;
    text-align: center;
    font-weight: bolder;
}

/* Background Styling */

.backgroundAbout {
    background-size: cover;
    z-index: -1;
    height: 100%;
    padding-bottom: 50px;
}
.backgroundWork {
    background-color: white;
    width: 100%;
